* {
  box-sizing: border-box;
}

button {
  transition: 0.5s all;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: yellow;
  width: 250px;
  height: 50px;
}
button:hover {
  background-color: pink;
  color: white;
}
button:active {
  background-color: greenyellow;
  color: black;
}
button:focus {
  background-color: grey;
}

.wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.circle {
  margin-top: 10px;
  border-radius: 100%;
  background-color: green;
  width: 50px;
  height: 50px;
}
.circle:nth-child(2n) {
  background-color: red;
}
.circle:nth-child(1) {
  background-color: skyblue;
}
.circle::after {
  display: block;
  background-color: black;
  width: 50%;
  height: 4px;
  content: "";
}
.circle::before {
  display: block;
  transition: 0.5s;
  background-color: black;
  width: 0%;
  height: 2px;
  content: "";
}
.circle:hover::before {
  width: 100%;
}

p::first-line {
  color: pink;
}

p::first-letter {
  color: red;
  font-size: 34px;
}

p::selection {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
}